.section {
  padding: 20px 0;

  @media screen and (min-width: $bp-tablet){
    padding: 48px 0;
  }

  &__title {
    font-size: 32px;
    font-weight: 900;
    line-height: 1.5;
    text-align: center;
    color: var(--color-text-secondary);

    @media screen and (min-width: $bp-tablet){
      font-size: 64px;
      line-height: 1.3;
    }
  }
}
